// base colours
$black: #000000;
$white: #ffffff;
$light: #f1f8ff;
$blue: #245EB9;
$light-blue: #BFDEFF;
// brand colours
$brand-primary: $blue;
$brand-info: $light-blue;
// grays
$gray-base: $black;
$gray-darker: #1b1b1b;
$gray-dark: #2d2d2d;
$gray-light: #ccc;

$state-info-bg: $brand-primary;
$state-info-text: $white;

// fonts
$font-family-base: 'Gothic A1', sans-serif;
$font-size-base: 20px;
$font-size-h1: 70px;
$font-size-h2: 50px;
$font-size-h3: 30px;
$font-size-h4: 20px;

$headings-font-family: 'Roboto', sans-serif;
$headings-color: $brand-primary!important;
$headings-font-weight: 900;
$headings-line-height: 1;
$text-color: $gray-base;
$line-height-base: 1.625;

// utilities
$padding: 40px !default;
$padding-large: 60px !default;
$padding-small: 20px !default;
$padding-xs: 15px !default;
$padding-large-xs: 25px !default;
$padding-small-xs: 5px !default;

// links
$link-color: $text-color;

// buttons
$btn-border-radius-base: 10px;
// default
$btn-default-border: $brand-primary;
$btn-default-bg: $white;
$btn-default-color: $brand-primary;
// primary
$btn-primary-color: $white;
$btn-primary-bg: $brand-primary;
$btn-primary-border: $brand-primary;
// info
$btn-info-color: $white;
$btn-info-bg: $brand-primary;
$btn-info-border: $brand-primary;

// Navbar
$navbar-default-bg: $white;
$navbar-default-border: $light;
$navbar-default-link-color: $text-color;
$navbar-default-link-hover-color: $white;
$navbar-default-link-hover-bg: $brand-primary;
$navbar-default-link-active-color: $navbar-default-link-hover-color;
$navbar-default-link-active-bg: $navbar-default-link-hover-bg;

// Navs
// justified
$nav-tabs-justified-link-border-color: $brand-primary;
$nav-tabs-justified-active-link-border-color: $nav-tabs-justified-link-border-color;
// pills
$nav-pills-active-link-hover-color: $white;
$nav-pills-active-link-hover-bg: $brand-primary;
// tabs
$nav-tabs-border-color: $brand-primary;
$nav-tabs-link-hover-border-color: $nav-tabs-border-color;
$nav-tabs-active-link-hover-color: $white;
$nav-tabs-active-link-hover-bg: $brand-primary;
$nav-tabs-active-link-hover-border-color: $nav-tabs-border-color;

// Glyphicons
// $icon-font-path: "../assets/fonts/bootstrap/";

//List exclusion lists
$ul-exclusions: ':not(.nav):not(.menu):not(.pagination):not(.dropdown-menu):not(.toolbar-menu):not(.contextual-links):not(.links):not([role="tablist"]):not(.list-group):not(.list-inline):not(.select2-selection__rendered):not(.select2-results__options)';
$ol-exclusions: ":not(.breadcrumb)";

// alert
$alert-border-radius: 0;
$alert-info-bg: transparent;
$alert-info-border: $brand-info;
$alert-info-text: $text-color;

// Jumbotron
$jumbotron-heading-font-size: 96px;
$jumbotron-padding: 0;
$jumbotron-color: $text-color;
$jumbotron-bg: $light;

// input/forms
$input-border: $gray-dark;
$input-border-focus: $gray-dark;
$input-color: $text-color;
$input-color-placeholder: $input-color;

// table
$table-bg-active: $brand-info;